import { lang } from './Lang';
import { Colors } from './Colors';
import { DOM } from './DOM';
import { InitPageBase } from './InitPageBase';
import { API } from './API';
class InitTagPage extends InitPageBase {
    constructor() {
        super();
        this.init();
    }
    // 添加中间按钮
    appendCenterBtns() {
        DOM.addBtn('crawlBtns', Colors.blue, lang.transl('_抓取该tag的投稿')).addEventListener('click', () => {
            this.readyCrawl();
        });
    }
    async FetchPostList() {
        let data;
        if (this.nextUrl) {
            data = (await API.request(this.nextUrl));
        }
        else {
            data = await API.getTagPostListByUser(API.getURLPathField('creator'), API.getURLPathField('tag'));
        }
        console.log(data);
        this.afterFetchPostList(data);
    }
    async fetchPost() { }
}
export { InitTagPage };
